<h2 id="input-输入框" tabindex="-1"><a class="header-anchor" href="#input-输入框" aria-hidden="true">#</a> Input 输入框</h2>
<h3 id="用法" tabindex="-1"><a class="header-anchor" href="#用法" aria-hidden="true">#</a> 用法</h3>
<h4 id="不同尺寸" tabindex="-1"><a class="header-anchor" href="#不同尺寸" aria-hidden="true">#</a> 不同尺寸</h4>
<div class="component-box">
    <div class="component-box-top"><ClientOnly>
        <wd-input type="text" size="large" placeholder="请输入内容"></wd-input>
        <wd-input
          type="text"
        ></wd-input>
        <wd-input
          type="text"
          size="small"
        ></wd-input>
    </ClientOnly></div>
    <div class="component-box-bottom">
        <div class="component-title">不同尺寸的输入框</div>
        <div class="component-desc">
            可以通过设置size属性设置输入框的大小：small | large | default
        </div>
    </div>
</div> 
<details class="custom-container details"><summary>点击查看代码</summary>
<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>wd-input</span> <span class="token attr-name">type</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>text<span class="token punctuation">"</span></span> <span class="token attr-name">size</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>large<span class="token punctuation">"</span></span> <span class="token attr-name">placeholder</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>请输入内容<span class="token punctuation">"</span></span><span class="token punctuation">></span></span><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>wd-input</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>wd-input</span>
  <span class="token attr-name">type</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>text<span class="token punctuation">"</span></span>
<span class="token punctuation">></span></span><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>wd-input</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>wd-input</span>
  <span class="token attr-name">type</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>text<span class="token punctuation">"</span></span>
  <span class="token attr-name">size</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>small<span class="token punctuation">"</span></span>
<span class="token punctuation">></span></span><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>wd-input</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div></details>
<h4 id="限制长度" tabindex="-1"><a class="header-anchor" href="#限制长度" aria-hidden="true">#</a> 限制长度</h4>
<div class="component-box">
    <div class="component-box-top"><ClientOnly>
        <wd-input
          type="text"
          size="small"
          maxlength="10"
          show-word-limit
        ></wd-input>
    </ClientOnly></div>
    <div class="component-box-bottom">
        <div class="component-title">限制长度的输入框</div>
        <div class="component-desc">
            可以设置maxlength属性设置输入框的最多的字符数；
        </div>
    </div>
</div> 
<details class="custom-container details"><summary>点击查看代码</summary>
<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>wd-input</span>
  <span class="token attr-name">type</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>text<span class="token punctuation">"</span></span>
  <span class="token attr-name">size</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>small<span class="token punctuation">"</span></span>
  <span class="token attr-name">maxlength</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>10<span class="token punctuation">"</span></span>
  <span class="token attr-name">show-word-limit</span>
<span class="token punctuation">></span></span><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>wd-input</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div></details>
<h4 id="前置或者后置" tabindex="-1"><a class="header-anchor" href="#前置或者后置" aria-hidden="true">#</a> 前置或者后置</h4>
<div class="component-box">
    <div class="component-box-top"><ClientOnly>
        <wd-input type="text" placeholder="带前置内容的输入框">
          <template #prepend>https://</template>
        </wd-input>
        <wd-input type="text" placeholder="带后置内容的输入框">
          <template #append>.com</template>
        </wd-input>
    </ClientOnly></div>
    <div class="component-box-bottom">
        <div class="component-title">带前置或者后置的输入框</div>
        <div class="component-desc">
            设置name为prepend或者append的slot可以设置前置或者后置内容
        </div>
    </div>
</div> 
<details class="custom-container details"><summary>点击查看代码</summary>
<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>wd-input</span> <span class="token attr-name">type</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>text<span class="token punctuation">"</span></span> <span class="token attr-name">placeholder</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>带前置内容的输入框<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
  <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>template</span> <span class="token attr-name">#prepend</span><span class="token punctuation">></span></span>https://<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>template</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>wd-input</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>wd-input</span> <span class="token attr-name">type</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>text<span class="token punctuation">"</span></span> <span class="token attr-name">placeholder</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>带后置内容的输入框<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
  <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>template</span> <span class="token attr-name">#append</span><span class="token punctuation">></span></span>.com<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>template</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>wd-input</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div></details>
<h4 id="文本框" tabindex="-1"><a class="header-anchor" href="#文本框" aria-hidden="true">#</a> 文本框</h4>
<div class="component-box">
    <div class="component-box-top"><ClientOnly>
        <wd-input
          type="textarea"
          show-word-limit
          maxlength="50"
          v-model="textData"
          placeholder="这是一个文本框"
        ></wd-input>
    </ClientOnly></div>
    <div class="component-box-bottom">
        <div class="component-title">文本框</div>
        <div class="component-desc">
          默认是输入框，如果设置了type为textarea，则为文本框，文本框和输入框的属性和
          方法基本一致；
        </div>
    </div>
</div> 
<details class="custom-container details"><summary>点击查看代码</summary>
<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>wd-input</span>
  <span class="token attr-name">type</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>textarea<span class="token punctuation">"</span></span>
  <span class="token attr-name">show-word-limit</span>
  <span class="token attr-name">maxlength</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>50<span class="token punctuation">"</span></span>
  <span class="token attr-name">v-model</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>textData<span class="token punctuation">"</span></span>
  <span class="token attr-name">placeholder</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>这是一个文本框<span class="token punctuation">"</span></span>
<span class="token punctuation">></span></span><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>wd-input</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div></details>
<h3 id="属性" tabindex="-1"><a class="header-anchor" href="#属性" aria-hidden="true">#</a> 属性</h3>
<table>
<thead>
<tr>
<th style="text-align:center">参数</th>
<th style="text-align:center">说明</th>
<th style="text-align:center">类型</th>
<th style="text-align:center">可选值</th>
<th style="text-align:center">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">autosize</td>
<td style="text-align:center">当type为textarea时生效</td>
<td style="text-align:center">boolean</td>
<td style="text-align:center">true / false</td>
<td style="text-align:center">false</td>
</tr>
<tr>
<td style="text-align:center">clearable</td>
<td style="text-align:center">是否可清空内容</td>
<td style="text-align:center">boolean</td>
<td style="text-align:center">true / false</td>
<td style="text-align:center">false</td>
</tr>
<tr>
<td style="text-align:center">disabled</td>
<td style="text-align:center">是否可用</td>
<td style="text-align:center">boolean</td>
<td style="text-align:center">true / false</td>
<td style="text-align:center">true</td>
</tr>
<tr>
<td style="text-align:center">prefixIcon</td>
<td style="text-align:center">前置图标</td>
<td style="text-align:center">string</td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:center">suffixIcon</td>
<td style="text-align:center">后置图标</td>
<td style="text-align:center">string</td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:center">size</td>
<td style="text-align:center">组件大小</td>
<td style="text-align:center">string</td>
<td style="text-align:center">small / large / default</td>
<td style="text-align:center">default</td>
</tr>
<tr>
<td style="text-align:center">show-password</td>
<td style="text-align:center">是否显示密码，当type为password时生效</td>
<td style="text-align:center">boolean</td>
<td style="text-align:center">true / false</td>
<td style="text-align:center">false</td>
</tr>
<tr>
<td style="text-align:center">show-word-limit</td>
<td style="text-align:center">是否显示字数限制提示</td>
<td style="text-align:center">boolean</td>
<td style="text-align:center">true / false</td>
<td style="text-align:center">false</td>
</tr>
<tr>
<td style="text-align:center">type</td>
<td style="text-align:center">文本框类型</td>
<td style="text-align:center">string</td>
<td style="text-align:center">参考<a href="https://developer.mozilla.org/zh-CN/docs/Web/HTML/Element/Input" target="_blank" rel="noopener noreferrer">原生type<OutboundLink/></a></td>
<td style="text-align:center">text</td>
</tr>
</tbody>
</table>
<h3 id="事件" tabindex="-1"><a class="header-anchor" href="#事件" aria-hidden="true">#</a> 事件</h3>
<table>
<thead>
<tr>
<th style="text-align:center">事件名称</th>
<th style="text-align:center">说明</th>
<th style="text-align:center">回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">input</td>
<td style="text-align:center">输入过程中触发</td>
<td style="text-align:center">Function(inputValue)</td>
</tr>
<tr>
<td style="text-align:center">change</td>
<td style="text-align:center">输入框的值变化时触发</td>
<td style="text-align:center">Function(inputValue)</td>
</tr>
<tr>
<td style="text-align:center">clear</td>
<td style="text-align:center">点击情况按钮时触发</td>
<td style="text-align:center">(event) =&gt; void</td>
</tr>
</tbody>
</table>
